import React, { useState, useCallback } from 'react'

import {
  useBrowserTheme,
  useBrowserThemeChangeListener,
} from 'react-use-browser-theme'

const App = () => {
  const browserTheme = useBrowserTheme()
  const [theme, setTheme] = useState(browserTheme)

  const browserThemeListener = useCallback(
    (e) => setTheme(e.matches ? 'dark' : 'light'),
    []
  )

  useBrowserThemeChangeListener(browserThemeListener)

  return (
    <div
      style={{
        minHeight: '100vh',
        minWidth: '100vw',
        color: `${theme === 'dark' ? 'white' : 'black'}`,
        backgroundColor: `${theme === 'dark' ? 'black' : 'white'}`,
      }}
    >
      Current browser theme: {theme}
    </div>
  )
}

export default App
